import axios from "axios";

import storeToken from "../action creator/storeToken";
import storeError from "../action creator/storeError";

import { ERROR_FETCHING_TOKEN } from "../core/constants";

export default function(reset) {
  return function(dispatch) {
    return axios
      .post("/token")
      .then(({ data }) => {
        dispatch(storeToken(data.token));
      })
      .catch(e => dispatch(storeError(ERROR_FETCHING_TOKEN)));
  };
}



