import React, { Component } from "react";

import HyperLink from "../hyperLink/HyperLink";

import "./Header.css";

class Header extends Component {
  render() {
    return (
      <nav className="header purple-bg">
        <HyperLink
          className="navbar-brand"
          addressUrl="https://www.geektrust.in/"
        >
          Geek Trust
        </HyperLink>
        <HyperLink clickHandler={this.props.reset}>Reset</HyperLink>
      </nav>
    );
  }
}

export default Header;



