import React, { Component } from "react";
import { PropTypes } from "prop-types";

import "./Dropdown.css";

class Dropdown extends Component {
  constructor(props) {
    super(props);

    this.emptyListText = "No Results";
    this.state = {
      options: props.data,
      inputText: props.selectedValue,
      show: false
    };
  }

  componentWillReceiveProps(newProps) {
    const inputText = newProps.selectedValue;

    this.setState({
      options: newProps.data,
      inputText
    });
    this.updateList(inputText, newProps);
  }

  clickHandler = value => {
    this.selectOption(value);
  };

  keyPresshandler = event => {
    //on pressing enter the first option in dropdown is selected
    if (event.key === "Enter") {
      const currentOptions = this.state.options;
      this.selectOption(currentOptions[0]);
      this.inputRef.blur();
    }
  };

  selectOption = value => {
    //selects the option only if the option is not no results
    if (value === this.emptyListText) {
      return;
    }
    this.props.onSelect(value);
    this.changeOptionsState(false);
  };

  showOptions = () => {
    this.inputRef.placeholder = "";
    this.changeOptionsState(true);
  };

  hideOptions = () => {
    setTimeout(() => this.changeOptionsState(false), 300);
    const value = this.inputRef.value;
    if (value === "") {
      this.inputRef.placeholder = this.props.placeholder;
    }
  };

  toggleOptions = () => {
    const currentState = this.state.show;
    this.changeOptionsState(!currentState);
  };

  changeOptionsState = state => {
    this.setState({ show: state });
  };

  handleChange = event => {
    //update the inputText state based on what user types in text box
    const inputText = event.target.value;
    this.setState({ inputText });

    this.updateList(inputText, this.props);
  };

  updateList(inputText, props) {
    //based on input text this function filters the options
    let list = props.data;
    list = list.filter(element => {
      return this.caseInsensitiveSearch(element, inputText);
    });
    this.setState({ options: list });
  }

  caseInsensitiveSearch = (element, inputText) => {
    //performs case insensitive search
    element = element.toLowerCase();
    inputText = inputText.toLowerCase();
    return element.search(inputText) !== -1;
  };

  populateOptions = list => {
    //this renders the options as html dropdown options
    list = this.processList(list);

    list = list.map((item, index) => {
      return (
        <a
          className="dropdown-item white-text"
          key={index}
          onClick={e => this.clickHandler(item)}
        >
          {item}
        </a>
      );
    });

    return <div className="dropdown-content purple-bg">{list}</div>;
  };

  processList = list => {
    //if list has options this functions returns the list else it sends list with no result string
    if (list.length === 0) {
      list.push(this.emptyListText);
    }

    return list;
  };

  render() {
    return (
      <div className="dropdown-wrapper">
        <div className="dropdown-header">
          <input
            type="name"
            className="dropdown-textbox purple-bg white-text"
            ref={input => (this.inputRef = input)}
            placeholder={this.props.placeholder}
            value={this.state.inputText}
            onChange={this.handleChange}
            onKeyPress={this.keyPresshandler}
            onFocus={this.showOptions}
            onBlur={this.hideOptions}
          />
          <span className="arrow-down" onClick={this.toggleOptions} />
        </div>
        {this.state.show && this.populateOptions(this.state.options)}
      </div>
    );
  }
}

Dropdown.propTypes = {
  placeholder: PropTypes.string.isRequired,
  data: PropTypes.arrayOf(PropTypes.string).isRequired,
  selectedValue: PropTypes.string.isRequired,
  onSelect: PropTypes.func.isRequired
};

export default Dropdown;



