import React from "react";

import Center from "../center/Center";
import HyperLink from "../hyperLink/HyperLink";

import "./Footer.css";

const Footer = props => {
  return (
    <nav className="footer purple-bg">
      <Center>
        <HyperLink addressUrl="https://www.geektrust.in/coding-problem/frontend/space">
          Visit GeekTrust to view the UI challenge
        </HyperLink>
      </Center>
    </nav>
  );
};

export default Footer;



