import axios from "axios";

import storeVehicles from "../action creator/storeVehicles";
import storeError from "../action creator/storeError";

import { ERROR_FETCHING_VEHICLES } from "../core/constants";

export default function(reset) {
  return function(dispatch) {
    return axios
      .get("/vehicles")
      .then(({ data }) => {
        dispatch(storeVehicles(data));
      })
      .catch(e => dispatch(storeError(ERROR_FETCHING_VEHICLES)));
  };
}



