import React, { Component } from "react";

import Center from "../center/Center";
import Title from "../title/Title";
import Paragraph from "../paragraph/Paragraph";
import Button from "../button/Button";

import FetchToken from "../../services/FetchToken";
import FetchPlanets from "../../services/FetchPlanets";
import FetchVehicles from "../../services/FetchVehicles";
import PostFind from "../../services/PostFind";

import resetErrors from "../../action creator/resetErrors";
import changeLoadingState from "../../action creator/changeLoadingState";

import {
  ERROR_FETCHING_TOKEN,
  ERROR_FETCHING_PLANETS,
  ERROR_FETCHING_VEHICLES,
  ERROR_POST_FIND
} from "../../core/constants";

import "./DisplayErrors.css";

export class DisplayErrors extends Component {
  hasErrors = errors => {
    return errors.length !== 0;
  };

  transformErrorsToMessages = errors => {
    //transforms error code to appropriate message
    const errorMessages = {
      ERROR_FETCHING_TOKEN: "Error occurred while fetching token",
      ERROR_FETCHING_PLANETS: "Error occurred while fetching planets",
      ERROR_FETCHING_VEHICLES: "Error occurred while fetching vehicles",
      ERROR_POST_FIND: "Error occurred while finding Falcone"
    };

    return errors.map((item, index) => (
      <Paragraph key={index}>{errorMessages[item]}</Paragraph>
    ));
  };

  fetchData = () => {
    //performs the failed network requests
    this.props.dispatch(changeLoadingState(true));
    this.props.dispatch(resetErrors());
    const errors = this.props.errors;
    const listOfFunction = errors.map(errorCode =>
      this.errorTodispatch(errorCode)
    );
    Promise.all(listOfFunction).then(() =>
      this.props.dispatch(changeLoadingState(false))
    );
  };

  errorTodispatch(errorCode) {
    //transforms error code to appropriate network requests
    switch (errorCode) {
      case ERROR_FETCHING_TOKEN:
        return this.props.dispatch(FetchToken());
      case ERROR_FETCHING_PLANETS:
        return this.props.dispatch(FetchPlanets());
      case ERROR_FETCHING_VEHICLES:
        return this.props.dispatch(FetchVehicles());
      case ERROR_POST_FIND:
        const postData = {
          token: this.props.token,
          planet_names: this.props.selectedTrips.map(item => item.planet),
          vehicle_names: this.props.selectedTrips.map(item => item.vehicle)
        };
        return this.props.dispatch(PostFind(postData));
    }
  }

  render() {
    return (
      <React.Fragment>
        {this.hasErrors(this.props.errors) && (
          <div className="fixed-full-screen black-bg">
            <Center>
              <Title>ERRORS</Title>
              {this.transformErrorsToMessages(this.props.errors)}
              <Button clickHandler={this.fetchData}>TRY AGAIN</Button>
            </Center>
          </div>
        )}
      </React.Fragment>
    );
  }
}

export default DisplayErrors;



