import { STORE_ERROR, RESET_ERRORS } from "../core/actions";

export default function(state = [], action) {
  switch (action.type) {
    case STORE_ERROR:
      return [...state, action.payload];
    case RESET_ERRORS:
      return [];
    default:
      return state;
  }
}



