import React, { Component } from "react";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";

import FetchToken from "../../services/FetchToken";

import storeResult from "../../action creator/storeResult";
import resetSelectedTrip from "../../action creator/resetSelectedTrip";

import Title from "../../components/title/Title";
import Paragraph from "../../components/paragraph/Paragraph";
import Button from "../../components/button/Button";
import Center from "../../components/center/Center";

import isObjectEmpty from "../../utils/isObjectEmpty";

import "./ResultScreen.css";

export class ResultScreen extends Component {
  constructor(props) {
    super(props);

    //if result is empty then moves to intro page
    if (isObjectEmpty(props.result)) {
      props.history.push("/");
    }
  }

  displayTitle = result => {
    if (result.status === "success") {
      return "SUCCESS";
    } else {
      return "FAILURE";
    }
  };

  displayMessage = result => {
    //transforms the status into detailed message
    if (result.status === "success") {
      return (
        "Queen AI Falcone is found at planet " +
        result.planet_name +
        " and exile extended for 15 more years."
      );
    } else {
      return "Queen AI Falcone is not found.";
    }
  };

  reset = () => {
    //fetches new token, empties result and empties selectedTrips
    this.props.dispatch(FetchToken());
    this.props.dispatch(resetSelectedTrip());
    this.props.dispatch(storeResult({}));
    this.props.history.push("/");
  };

  calculateTimeTaken = props => {
    const message = "TIME TAKEN: ";
    let timeTaken = 0;
    props.selectedTrips.forEach(item => {
      const planetObject = props.planets.find(
        planet => planet.name === item.planet
      );
      const vehicleObject = props.vehicles.find(
        vehicle => vehicle.name === item.vehicle
      );
      timeTaken += planetObject.distance / vehicleObject.speed;
    });
    return message + timeTaken;
  };

  render() {
    return (
      <div className="half-container">
        <Title>{this.displayTitle(this.props.result)}</Title>
        <Paragraph className="time-taken">
          {this.calculateTimeTaken(this.props)}
        </Paragraph>
        <Center>
          <Paragraph>{this.displayMessage(this.props.result)}</Paragraph>
          <Button clickHandler={this.reset}>TRY AGAIN</Button>
        </Center>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    result: state.result,
    selectedTrips: state.selectedTrips,
    planets: state.planets,
    vehicles: state.vehicles
  };
};

const mapDispatchToProps = dispatch => {
  return {
    dispatch
  };
};

export default withRouter(
  connect(mapStateToProps, mapDispatchToProps)(ResultScreen)
);



