import axios from "axios";

import storePlanets from "../action creator/storePlanets";
import storeError from "../action creator/storeError";

import { ERROR_FETCHING_PLANETS } from "../core/constants";

export default function(reset) {
  return function(dispatch) {
    return axios
      .get("/planets")
      .then(({ data }) => {
        dispatch(storePlanets(data));
      })
      .catch(e => dispatch(storeError(ERROR_FETCHING_PLANETS)));
  };
}



