import React, { Component } from "react";
import { connect } from "react-redux";
import { Route, withRouter, Switch } from "react-router-dom";

import Header from "../../components/header/Header";
import Content from "../../components/content/Content";
import Footer from "../../components/footer/Footer";
import Loader from "../../components/loader/Loader";
import ErrorScreen from "../../components/displayErrors/DisplayErrors";

import IntroScreen from "../introScreen/IntroScreen";
import DetailsScreen from "../detailsScreen/DetailsScreen";
import PlanningScreen from "../planningScreen/PlanningScreen";
import ResultScreen from "../resultScreen/ResultScreen";

import changeLoadingState from "../../action creator/changeLoadingState";
import storeResult from "../../action creator/storeResult";
import resetSelectedTrip from "../../action creator/resetSelectedTrip";

import FetchToken from "../../services/FetchToken";
import FetchPlanets from "../../services/FetchPlanets";
import FetchVehicles from "../../services/FetchVehicles";

import "./RootScreen.css";

export class RootScreen extends Component {
  constructor(props) {
    super(props);

    //fetches token, list of planets and vehicles
    props.fetchData();
  }

  reset = () => {
    //fetches a new token and moves to intro page
    this.props.reset();
    this.props.history.push("/");
  };

  render() {
    return (
      <React.Fragment>
        <Header reset={this.reset} />
        <Content>
          <Switch>
            <Route exact path="/" component={IntroScreen} />
            <Route path="/details" component={DetailsScreen} />
            <Route path="/planning" component={PlanningScreen} />
            <Route path="/result" component={ResultScreen} />
          </Switch>
        </Content>
        <Footer />
        <Loader show={this.props.loading} />
        <ErrorScreen
          errors={this.props.errors}
          token={this.props.token}
          selectedTrips={this.props.selectedTrips}
          dispatch={this.props.dispatch}
        />
      </React.Fragment>
    );
  }
}

const mapStateToProps = state => {
  return {
    loading: state.loading,
    errors: state.errors,
    token: state.token,
    selectedTrips: state.selectedTrips
  };
};

const mapDispatchToProps = dispatch => {
  return {
    fetchData: () => {
      dispatch(changeLoadingState(true));
      Promise.all([
        dispatch(FetchToken()),
        dispatch(FetchPlanets()),
        dispatch(FetchVehicles())
      ]).then(() => dispatch(changeLoadingState(false)));
    },
    reset: () => {
      dispatch(FetchToken());
      dispatch(resetSelectedTrip());
      dispatch(storeResult({}));
    },
    dispatch
  };
};

export default withRouter(
  connect(mapStateToProps, mapDispatchToProps)(RootScreen)
);



