import {
  STORE_SELECTED_TRIP,
  REMOVE_SELECTED_TRIP,
  RESET_SELECTED_TRIP
} from "../core/actions";

import removeObjectFromArray from "../utils/removeObjectFromArray";

export default function(state = [], action) {
  switch (action.type) {
    case STORE_SELECTED_TRIP:
      return [...state, action.payload];
    case REMOVE_SELECTED_TRIP:
      return removeObjectFromArray(state, action.payload);
    case RESET_SELECTED_TRIP:
      return [];
    default:
      return state;
  }
}



