import React, { Component } from "react";
import { PropTypes } from "prop-types";

import "./Title.css";

class Title extends Component {
  defaultClassesList = "white-text text-center ";
  render() {
    return (
      <h1 className={this.defaultClassesList + this.props.className}>
        {this.props.children}
      </h1>
    );
  }
}

Title.propTypes = {
  className: PropTypes.string
};

Title.defaultProps = {
  className: ""
};

export default Title;



