import React, { Component } from "react";
import { PropTypes } from "prop-types";

import Paragraph from "../paragraph/Paragraph";
import Button from "../button/Button";
import Center from "../center/Center";

import "./ListItem.css";

class ListItem extends Component {
  remove = () => {
    const tripInfo = {
      planet: this.props.planet,
      vehicle: this.props.vehicle
    };
    this.props.remove(tripInfo);
  };

  render() {
    return (
      <div className="col-1">
        <Paragraph className="custom-paragraph">{this.props.vehicle}</Paragraph>
        <Paragraph className="custom-paragraph">TO</Paragraph>
        <Paragraph className="custom-paragraph">{this.props.planet}</Paragraph>
        <Center>
          <Button
            className="transparent-bg red-text"
            clickHandler={this.remove}
          >
            REMOVE
          </Button>
        </Center>
      </div>
    );
  }
}

ListItem.propTypes = {
  planet: PropTypes.string.isRequired,
  vehicle: PropTypes.string.isRequired,
  remove: PropTypes.func.isRequired
};

export default ListItem;



