import React, { Component } from "react";
import { PropTypes } from "prop-types";
import Modal from "react-modal";

import Center from "../center/Center";
import CardsList from "../cardsList/CardsList";

import "./Modal.css";

const styles = {
  content: {
    width: "70%",
    height: "50%",
    top: "50%",
    left: "50%",
    transform: "translate(-50%,-50%)",
    padding: 0,
    border: 0
  },
  overlay: {
    backgroundColor: "rgba(0, 0, 0, 0.75)"
  }
};

class SimpleModal extends Component {
  render() {
    return (
      <Modal style={styles} isOpen={this.props.show}>
        <div className="modal-header purple-bg">
          <p className="modal-header-title white-text text-start">
            {this.props.title}
          </p>
          <button
            className="btn modal-close-btn transparent-bg white-text"
            onClick={this.props.closeModal}
          >
            X
          </button>
        </div>
        <div className="modal-content">
          <Center>
            <CardsList data={this.props.data} />
          </Center>
        </div>
      </Modal>
    );
  }
}
SimpleModal.propTypes = {
  show: PropTypes.bool.isRequired,
  data: PropTypes.arrayOf(PropTypes.object).isRequired,
  title: PropTypes.string.isRequired,
  closeModal: PropTypes.func.isRequired
};

export default SimpleModal;



