import axios from "axios";

import storeResult from "../action creator/storeResult";
import storeError from "../action creator/storeError";

import { ERROR_POST_FIND } from "../core/constants";

export default function(postData) {
  return function(dispatch) {
    return axios
      .post("/find", postData)
      .then(({ data }) => {
        dispatch(storeResult(data));
      })
      .catch(e => dispatch(storeError(ERROR_POST_FIND)));
  };
}



