import React, { Component } from "react";
import { PropTypes } from "prop-types";

import "./Card.css";

class Card extends Component {
  displayContent = data => {
    return data.map((item, index) => {
      if (item.type === "image") {
        const img = require("../../../assets/" + item.value + ".PNG");
        return (
          <img key={index} className="card-img" src={img} alt={item.value} />
        );
      } else {
        return (
          <p key={index} className="card-paragraph">
            {item.value}
          </p>
        );
      }
    });
  };

  render() {
    return <div className="card">{this.displayContent(this.props.data)}</div>;
  }
}

Card.propTypes = {
  data: PropTypes.arrayOf(PropTypes.object).isRequired
};

export default Card;



